//----------------- Переменные ---------------------------------

$fontfamily: 'PT Sans';

$minwidth: 320px;
$mw: 950;
$md1: $mw + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//-----------------Оболочка---------------------------------
.wrapper {
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    &._loaded{

    }
}

//----------------- Основная сетка ---------------------------------
._container {
    max-width: $mw + px;
    margin: 0 auto;
    padding: 0 10px;
    // @media(max-width: $md1 + px) {
    //     max-width: 970px;
    // }
    // @media(max-width: $md2 + px) {
    //     max-width: 750px;
    // }
    @media(max-width: $md3 + px) {
        max-width: none;
        padding: 0 10px;
    }
}

._ibg {
    position: relative;
  }
  
._ibg img {
position: absolute;
width: 100%;
height: 100%;
top: 0;
left: 0;
object-fit: cover;
}



//----------------- Общие параметры ---------------------------------
body {
    color: #000;
    font-size: 14px;
    &._lock{
        overflow: hidden;
        @media (max-width: $md3 + px) {
            //width: 100%;
            //position: fixed;
            //overflow: hidden;
        }
    }
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
}

input {
    width: 100%;
    border-radius: 0 !important;
    border: none;
    display: block;
    padding: 0 25px;
    height: 100%;
    color: #4f4f4f;
    font-size: 15px;
    outline: none;
    &:focus {
        color: #000;
    }
}
button {
    border: none;
    cursor: pointer;
}

//----------------- Main styles ---------------------------------

.header {
    background-color: #1c242a;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 50;
    &__container {
        position: relative;
        display: flex;
        align-items: center;
        height: 75px;
        @media (max-width: $md3+px) {
            justify-content: space-between;
        }
    }
    &__logo {
        width: 144px;
        position: relative;
        z-index: 5;
        @media (max-width: $md3+px) {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.menu {
    flex: 1 1 auto;
    padding-left: 30px/950px*100;
    @media (max-width: $md3+px) {
        flex: 0 0 auto;
    }
    &__link {
        color: #fff;
        letter-spacing: 1.4px;
        font-family: 'pfdintextcondpro-regular';
        text-transform: uppercase;
        @media (min-width: 992px) {
            &:hover {
                text-decoration: underline;
            }
        }
        @media (max-width: $md3+px) {
            font-size: 24px;
        }
    }
    &__list {
        display: flex;
        justify-content: center;
        li {
            margin-right: 15px;
        }
        @media (max-width: $md3+px) {
            display: block;
            text-align: center;
            li {
                margin-bottom: 30px;
                margin-right: 0;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    &__body {
        @media (max-width: $md3+px) {
            background-color: rgba(28, 36, 42, 0.8);
            position: fixed;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            overflow: auto;
            padding: 100px 0 20px 0;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease;
            &:after {
                content: '';
                background-color: #1c242a;
                position: fixed;
                top: 0;
                left: 0;
                height: 75px;
                width: 100%;
            }
            &._active{
                opacity: 1;
                visibility: visible;
                z-index: 4;
            }
        }
        
    }
}

.icon-menu {
    display: none;
    &._active {
      span{
          &:first-child {
            transform: rotate(45deg) translateY(11px);
            margin-left: 7px;
          }
          &:nth-child(2) {
              opacity: 0;
          }
          &:last-child {
            transform: rotate(-45deg) translateY(-11px);
            margin-left: 7px;
          }
      }      
    }
    @media(max-width: $md3+px) {
        display: block;
        position: relative;
        width: 30px;
        height: 18px;
        cursor: pointer;
        z-index: 5;
    }
    span {
        top: calc(50% - 1px);
        left: 0;
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #bb9c66;
        transition: all 0.3s ease;
        &:first-child{
            top: 0;
        }
        &:last-child {
           top: auto;
           bottom: 0;;
        }
    }
}

.actions-header {
    display: flex;
    align-items: center;
    &__region {
        position: relative;
        z-index: 4;
        @extend .menu__link;
        padding-left: 30px;
        line-height: 18px;
        min-width: 18px;
        min-height: 18px;
        margin-bottom: 2px;
        background: url(../img/globe.png) left 0 no-repeat;
        span {
            margin-top: 2px;
            display: inline-block;
        }
        @media (max-width: 890px) {
            padding: 0;
            span {
                display: none;
            }
        }
        // @media (max-width: $md3+px){
        //     padding-left: 30px;
        //     span {
        //         display: inline-block;
        //     }
        // }
    }
    &__user {
        margin-left: 30px;
        z-index: 4;
        @media (max-width: 890px) {
            margin-left: 15px;
        }
    }
}

.user-header {
    &__icon {
        cursor: pointer;
        position: relative;
        z-index: 2;
    }
    &__menu {
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease;
        position: absolute;
        top: 88px;
        right: 0;
        min-width: 176px;
        background-color: #1c242a;
        padding: 20px;
        z-index: 4;
        li {
                margin-bottom: 8px;
                &:last-child {
                    margin-bottom: 0;
                }
        }
        @media (max-width: $md3+px) {
           width: 100%;
        }
        &._active {
            opacity: 1;
            visibility: visible;
            top: 78px;
        }
    }
    &__link {
        color: #fff;
        padding-left: 30px;
        display: inline-block;
        line-height: 18px;
        font: 14px;
        &_1 {
            background: url(../img/user-header-link-1.png) 1px center no-repeat;
        }
        &_2 {
            background: url(../img/user-header-link-2.png) left center no-repeat;
        }
        &_3 {
            background: url(../img/user-header-link-3.png) 4px center no-repeat;
        }
        &_4 {
            background: url(../img/user-header-link-4.png) left center no-repeat;
        }
        @media (min-width: 992px) {
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.page {
    flex: 1 1 auto;
    padding-top: 75px;
}

.title {
    text-align: center;
    &__item {
        font-family: 'ceremonious_one';
        color: #bb9c66;
        font-size: 40px;
        margin: 0 0 20px/40px * 1em 0;
    }
    &__main {
        font-family: 'pfdintextcondpro-medium';
        letter-spacing: 5.25px;
        font-size: 35px;
        text-transform: uppercase;
        &_regular {
            font-family: 'pfdintextcondpro-regular';
        }
        &_w {
            color: #fff;
        }
    }
}

.item-main-slider {
    text-align: center;
    padding: 30px 0 100px;
    position: relative;
    &__content {
        position: relative;
        z-index: 2;
        height: 100%;
    }
    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

.item-main-slider{
    &__text {
        color: #fff;
        font-size: 15px;
        line-height: 165%;
        max-width: 700px;
        margin: 25px auto 0 auto;
        flex: 1 1 auto;
    }
    &__container {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__btn {
        margin-top: 55px;

    }
}

.btn {
    position: relative;
    display: inline-block;
    &:before,
    &:after {
        content: '';
        position: absolute;
        left: 0;
        height: 3px;
        width: 100%;
    }
    &:before {
        background-color: #c3a774;
        top: 0;
    }
    &:after {
        background-color: #8f6630;
        bottom: 0;;
    }
    &_fw {
        width: 100%;
    }
    &__body {
        padding: 0;
        &:before,
        &:after {
            content: '';
            position: absolute;
            top: 0;
            height: 100%;
            width: 3px;
            background-image: linear-gradient(to top, #8f6630, #c3a774);
        }
        &:before {
            left: 0;
        }
        &:after {
            right: 0;
        }
        span {
            padding: 0 50px;
            color: #fff;
            font-size: 15px;
            text-transform: uppercase;
            font-family: 'pfdintextcondpro-regular';
            letter-spacing: 2px;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            position: relative;
            min-height: 50px;
            &:after {
                content: '';
                height: 50%;
                opacity: 0.06;
                background-image: linear-gradient(to top, #bb9c66 0%, rgba(187, 156, 102, 0) 100%);
                top: 0;
                left: 0;
                position: absolute;
                width: 100%;
                transition: all 0.5s ease;
            }
        }
    }
    &__big {
        .btn__body span {
            font-size: 21px;
            color: #000;
            font-family: 'pfdintextcondpro-medium';
            letter-spacing: 0;
            padding: 0;
        }  
    }
    @media (min-width: 992px) {
        &:hover {
            .btn__body span {
                &:after {
                    height: 100%;
                    opacity: 0.3;
                }
            }
        }
    }
}

.ring {
    display: inline-block;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    vertical-align: middle;
}

.main-slider {
    position: relative;
    &__control {
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 100px;
        height: 100px;
        transform: translate(-50%, 50%);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 20;
        &:after {
            content: '';
            background-color: #bb9c66;
            opacity: 0.87;
            position: absolute;
            width: 70%;
            height: 70%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

.control-main-slider {
    &__arrow {
        position: relative;
        z-index: 2;
        background: url(../img/arrow-prev.png) 0 0 no-repeat;
        width: 14px;
        height: 24px;
        cursor: pointer;
        &_next {
            transform: rotate(-180deg);
        }
    }
    &__decor {
        position: relative;
        z-index: 2;
        margin: 0 12px;
        animation: rotate 5s infinite linear;
    }
}

@keyframes rotate {
    0%{

    }
    100% {
        transform: rotate(360deg)
    }
}

.lots {
    padding: 85px 0 62px;
    &__header {
        margin-bottom: 15px;
    }
}

.block-header {
    &__title {
        
    }
}

.block-decor {
    display: flex;
    align-items: center;
    &__items {
        flex: 1 1 auto;
        display: flex;
        margin-bottom: 8px;
        align-items: center;
        &:before {
            content: '';
            height: 1px;
            width: 100%;
            background-color: #bb9c66;
        }
        &:after {
            content: '';
            height: 25px * 0.72;
            border: 1px solid #bb9c66;
            transform: rotate(-45deg);
            flex: 0 0 25px * 0.72;
            margin: 0 5px 0 5px;
        }
        span {
            display: flex;
            align-items: center;
            margin: 0 5px;
            &:before {
                content: '';
                width: 10px;
                flex: 0 0 10px;
                height: 10px;
                border-radius: 50%;
                border: 1px solid #bb9c66;
                margin: 0 5px;
            }
            &:after {
                content: '';
                height: 15px * 0.72;
                width: 15px * 0.72;
                border: 1px solid #bb9c66;
                transform: rotate(-45deg);
                flex: 0 0 15px * 0.72;
            }
        }
        &:last-child {
            span {
                flex-direction: row-reverse;
            }
            flex-direction: row-reverse;
        }
    }
    &__icon {
        flex: 0 0 140px;
        text-align: center;
        img {
            max-width: 70px;
        }
        @media (max-width: $md3+px){
            flex: 0 0 95px;
        }
    }
}

.control-slider-lots {
    display: flex;
    justify-content: center;
    align-items: center;
    &__link {
        background: url(../img/circle.png) 0 0 no-repeat;
        flex: 0 0 100px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 21px;
        color: #000;
        font-weight: 500;
        font-family: 'pfdintextcondpro-medium';
        text-transform: uppercase;
        margin: 0 20px;
    }
    &__arrow {
        display: flex;
        align-items: center;
        cursor: pointer;
        justify-content: flex-end;
        span {
            background: url(../img/lots-arrow.png) left no-repeat;
            cursor: pointer;
            width: 108px;
            height: 35px;
            flex: 0 0 108px;
            overflow: hidden;
            @media(max-width: 450px){
                flex: 0 0 50px;
            }
        }
        &:after {
            content: '';
            margin: 0 5px;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            border: 1px solid #bb9c66;
        }
        &:last-child {
            flex-direction: row-reverse;
            span {
                transform: rotate(-180deg);
            }
        }
    }
}

.lot {
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    &__image {
        display: block;
        max-width: 250px;
        padding-bottom: 100%;
        @media(max-width: 975px){
            max-width: 100%;
        }
    }
    &__header {
        text-align: center;
        margin-bottom: 30px;
        position: relative;
    }
    &__title {
        font-size: 21px;
        font-weight: 500;
        font-family: 'pfdintextcondpro-medium';
        color: #000;
        text-transform: uppercase;
        line-height: calc(25/21);
        //width: 250px;
        display: inline-block;
        flex: 1 1 auto;
        position: relative;
        z-index: 2;
        margin: 0 0 30px/21px * 1em 0;
        @media(max-width: 975px){
            font-size: 18px;
        }
        @media(min-width: 992px){
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 25px;
        span {
            width: 100%;
        }
    }
}

.timer-lot {
    width: 72px;
    height: 72px;
    transform: translate(-50%, 50%);
    display: inline-flex;
    position: relative;
    align-items: center;
    flex-direction: column;
    padding-top: 2px;
    position: absolute;
    left: 50%;
    bottom: 0;
    div {
        position: relative;
        z-index: 2;
    }
    &:before {
        content: '';      
        background-color: #fff;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transform: rotate(-45deg);
    }
    &__item {
        font-size: 18px;
        font-family: 'PT Sans Narrow', sans-serif;
    }
    &__icon {
        margin-bottom: 1px;
    }
}

.slider-lots {
    &__body {
        display: flex;
        margin: 0 -50px 50px;
        align-items: stretch;
        @media(max-width: 975px){
            margin: 0 -15px 50px;
        }
    }
    &__slide {
        padding: 0 46px;
        flex: 1 1 33%;
        @media(max-width: 975px){
            padding: 0 12px;
        }
    }
}

.info-lot {
    display: flex;
    justify-content: center;
    margin-bottom: 35px;
    &__statistic {
        flex: 0 0 50px;
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    &__icon {
        width: 25px * 0.72;
        height: 25px * 0.72;
        margin: 0 auto 10px auto;
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        img {
            position: relative;
            z-index: 2;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &:after {
            content: '';
            position: absolute;
            background-color: #000;
            width: 100%;
            height: 100%;
            transform: rotate(-45deg);
        }
    }
    &__value {
        font-size: 14px;
        border-bottom: 1px solid #bb9c66;
        padding-bottom: 5px;
    }
    &__current-bet {
        flex: 1 1 auto;
        @media(max-width: 975px){
            flex: 0 0 auto;
        }
    }
}

.current-bet-info-lot {
    padding: 0 5px;
    position: relative;
    @media(max-width: 975px){
        padding: 0 10px;
    }
    &:after {
        content: '';
        position: absolute;
        border: 1px solid #bb9c66;
        width: 72px;
        height: 72px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
    }
    div {
        position: relative;
        z-index: 2;
    }
    &_label {
        margin-bottom: 5px;
    }
    &__value {
        font-family: 'BravoRGRegular';
        font-size: 50px;
        white-space: nowrap;
        @media(max-width: 975px){
            font-size: 35px;
        }
    }
}

.item-category {
    position: relative;
    padding: 30px 30px 40px 30px;
    height: 250px;
    width: 250px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @media(min-width: 992px){
        &:hover {
            .item-category__bg {
                transform: scale(1.1);
                &:after {
                    background-color: rgba(0, 0, 0, 0.5); 
                }
            }
        }
    }
    &__icon {
        position: relative;
        z-index: 2;
        margin-bottom: 22px;
    }
    &__title {
        position: relative;
        z-index: 2;
        color: #fff;
        text-transform: uppercase;
        font-family: 'pfdintextcondpro-medium';
        font-size: 21px;
        flex: 1 1 auto;
        letter-spacing: 2.1px;
        line-height: calc(25/21);
    }
    &__link {
       position: absolute;
       width: 100%;
       height: 100%;
       top: 0;
       left: 0;
       z-index: 5;
       border-radius: 50%;
    }
    &__decor {
        text-align: center;
        position: relative;
        z-index: 2;
    }
    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transition: all 0.3s ease;
        &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.7);
            transition: all 0.3s ease;
        }
    }
}

.categories {
    margin-bottom: 70px;
    @media(max-width: 767px){
        margin-bottom: 35px;
    }
    &__items {
        margin-bottom: 85px;
        @media(max-width: 767px){
            margin: 0;
        }
    }
    &__container {
        overflow: hidden;
        position: relative;
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            height: 45px;
            width: 100%;
            background-color: #fff;
        }
    }
    &__row {
        display: flex;
        margin: 0 -50px;
        position: relative;
        padding-top: 34px;
        flex-wrap: wrap;
        &:before, &:after {
            top: 4px;
            content: '';
            position: absolute;
            width: 24px * 0.72;
            height: 24px * 0.72;
            flex: 0 0 24px * 0.72;
            border: 1px solid #bb9c66;
            transform: rotate(-45deg);
        }
        &:before {
            left: 32.5%;
            @media(max-width: 975px){
                left: 49.1%;
            }
        }
        &:after {
            left: 65.8%;
            @media(max-width: 975px){
                display: none;
            }
        }
    }
    &__column {
        padding: 0 46px 25px;
        flex: 0 1 33.3333333%;
        margin-bottom: 25px;
        position: relative;
        display: flex;
        justify-content: center;
        &:before {
            content: '';
            width: 10px;
            height: 10px;
            background-color: #bb9c66;
            border-radius: 50%;
            flex: 0 0 10px;
            position: absolute;
            left: 50%;
            bottom: -5px;
            transform: translateX(-50%);
        }
        @media(max-width: 975px){
            flex: 0 1 50%;
        }
        @media(max-width: 650px){
            flex: 1 1 100%;
        }
    }
    &__decor {
        position: absolute;
        right: 0;
        top: 0;
        width: 24px;
        height: calc(100% + 9px);
        transform: translateX(50%);
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 5;
        &:before {
            content: '';
            background-color: #bb9c66;
            width: 1px;
            flex: 1 1 auto;
        }
        &:after {
            content: '';
            width: 24px * 0.72;
            height: 24px * 0.72;
            flex: 0 0 24px * 0.72;
            border: 1px solid #bb9c66;
            transform: rotate(-45deg);
            margin-top: 15px;
        }
        
        span {
            display: flex;
            flex-direction: column;
            align-items: center;
            flex: 1 1 auto;
            &:before {
                content: '';
                width: 10px;
                height: 10px;
                background-color: #bb9c66;
                border-radius: 50%;
                flex: 0 0 10px;
                margin: 20px 0;
            }
            &:after {
                content: '';
                background-color: #bb9c66;
                width: 1px;
                flex: 1 1 auto;
            }
        }
    }
}

